<template>
  <el-row v-loading="loading">
    <el-col :span="24">
      <div style="text-align: left;">
        <el-button type="text" icon="el-icon-back" @click="goBack" style="padding-bottom: 0px;">返回</el-button>
      </div>
    </el-col>
    <el-col :span="24">
      <div>
        <div>
          <h3 style="margin-top: 0px;margin-bottom: 0px">{{ article.title }}</h3>
        </div>
        <div style="width: 100%;margin-top: 5px;display: flex;justify-content: flex-end;align-items: center">
          <div style="display: inline; color: #00BFA5;margin-left: 50px;margin-right:20px;font-size: 12px;">
            {{ article.nickname }}
          </div>
          <span style="color: #00BFA5;margin-right:20px;font-size: 12px;">浏览
            {{ article.pageView == null ? 0 : article.pageView }}
          </span>
          <span style="color: #00BFA5;margin-right:20px;font-size: 12px;">
            {{ article.editTime | formatDateTime }}
          </span>
          <el-tag type="success" v-for="(item, index) in article.tags" :key="index" size="small" style="margin-left: 8px">
            {{ item.tagName }}
          </el-tag>
          <span style="margin:0px 50px 0px 0px"></span>
        </div>
      </div>
    </el-col>
    <el-col>
      <div style="text-align: left" v-html="article.htmlContent">
      </div>
    </el-col>
  </el-row>
</template>
<script>
import { getRequest } from '../utils/api'

export default {
  data() {
    return {
      article: {},
      loading: false,
    }
  },
  mounted: function () {
    if (this.$route.query?.aid != null) {
      let aid = this.$route.query.aid;
      let _this = this;
      this.loading = true;
      getRequest("/article/" + aid).then(resp => {
        if (resp.status == 200) {
          _this.article = resp.data;
        }
        _this.loading = false;
      }, resp => {
        _this.loading = false;
        _this.$message({ type: 'error', message: '页面加载失败!' });
      });
    } else {
      this.$message.error('无效的路由参数');
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
}
</script>
